.text-center {
  text-align: center;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.4rem;
  background-color: #1c2431;
}

p {
  font-size: 1.5rem;
  color: white;
}

.contenedor {
  width: 95%;
  max-width: 1300px;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: white;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
  color: white;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 2.2rem;
  line-height: 1.5;
  font-family: "Raleway", sans-serif;
}

h3 {
  font-size: 3.2rem;
}

.attribution {
  font-size: 11px;
  text-align: center;
}
.attribution a {
  color: #3e52a3;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3rem 1.5rem;
}
@media (min-width: 768px) {
  .header {
    padding: 6rem 4rem;
  }
}
.header__logo {
  width: 8rem;
}
@media (min-width: 768px) {
  .header__logo {
    width: 16rem;
  }
}
.header__link {
  padding-left: 2rem;
}
.header__link:first-child {
  padding-left: 0;
}

.bg {
  background: linear-gradient(to bottom, transparent 45%, #181f2a 45% 100%), url(../../build/img/bg-curvy-mobile.svg);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 0 35%;
}
@media (min-width: 768px) {
  .bg {
    background: url(../../build/img/bg-curvy-desktop.svg);
    background-position: -38% bottom;
    background-repeat: no-repeat;
    background-size: 100% 50%;
  }
}

.informacion {
  width: 85%;
  padding-top: 5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .informacion {
    padding-top: 0;
    width: 35%;
    padding-bottom: 17rem;
    margin: 0 auto;
  }
}
.informacion__imagen {
  color: white;
  height: auto;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .informacion__imagen {
    margin: 0 auto;
  }
}
@media (min-width: 768px) {
  .informacion__heading {
    margin-top: 2rem;
    margin-bottom: 0;
    font-size: 3rem;
  }
}
.informacion__descripcion {
  line-height: 1.5;
  font-size: 2.5rem;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .informacion__descripcion {
    font-size: 1.8rem;
  }
}
.informacion__boton {
  background: linear-gradient(to right, #65e2d9, #339ecc);
  font-size: 1.6rem;
  padding: 2rem 3rem;
  border-radius: 4rem;
  font-weight: 700;
  display: block;
  width: 80%;
  text-align: center;
  color: white;
  text-decoration: none;
  border: none;
  margin: 0 auto;
}
.informacion__boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .informacion__boton {
    width: auto;
  }
}
@media (min-width: 1200px) {
  .informacion__boton {
    width: 40%;
  }
}

.caracteristicas {
  background-color: #181f2a;
  padding-top: 12rem;
}
@media (min-width: 768px) {
  .caracteristicas {
    padding-top: 0;
  }
}
.caracteristicas__grid {
  width: 85%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .caracteristicas__grid {
    display: grid;
    gap: 10rem;
    grid-template-columns: repeat(2, 1fr);
    width: 60%;
  }
}

.box {
  margin-bottom: 8rem;
  text-align: center;
}
.box:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .box {
    margin-bottom: 0;
  }
}
.box__icon {
  margin: 0 auto;
  margin-bottom: 2rem;
}
.box__heading {
  font-size: 2rem;
  margin: 0;
  font-family: "Raleway", sans-serif;
}
.box__descripcion {
  line-height: 1.5;
}

.productive {
  background-color: #181f2a;
  padding-top: 20rem;
  margin-top: -5rem;
}
.productive__grid {
  width: 85%;
}
@media (min-width: 768px) {
  .productive__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.productive__heading {
  margin-top: 5rem;
  margin-bottom: 2rem;
  font-size: 1.8rem;
}
.productive__imagen {
  height: auto;
  margin: 0 auto;
}
.productive__texto {
  font-size: 1.4rem;
  line-height: 1.5;
}
@media (min-width: 768px) {
  .productive__texto {
    font-size: 1.8rem;
  }
}
.productive__mas {
  display: inline-block;
}
.productive__mas::after {
  content: "";
  display: block;
  background-color: #65e2d9;
  height: 0.3rem;
  width: 100%;
  margin: 0.7rem 0 0 0;
}
.productive__link {
  color: #65e2d9;
}
.productive__icon {
  display: inline;
}

.testimoniales {
  padding-top: 15rem;
  padding-bottom: 15rem;
  background-color: #181f2a;
}
.testimoniales__grid {
  width: 80%;
}
@media (min-width: 768px) {
  .testimoniales__grid {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.testimonial {
  background-color: #202a3c;
  padding: 2rem;
  border-radius: 1rem;
  max-width: 90%;
  margin-bottom: 2.5rem;
}
@media (min-width: 768px) {
  .testimonial {
    margin-bottom: 0;
  }
}
.testimonial:first-child {
  position: relative;
}
.testimonial__comillas {
  position: absolute;
  left: 0;
  top: -2rem;
  width: 2.5rem;
  height: auto;
}
@media (min-width: 768px) {
  .testimonial__comillas {
    width: 5rem;
    top: -4rem;
    left: -0.5rem;
  }
}
.testimonial__informacion {
  font-size: 1rem;
  line-height: 2;
}
@media (min-width: 768px) {
  .testimonial__informacion {
    font-size: 1.3rem;
    line-height: 1.7;
  }
}
.testimonial__autor {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .testimonial__autor {
    margin-top: 0;
  }
}
.testimonial__foto {
  width: 4rem;
  border-radius: 2rem;
  height: auto;
  margin-right: 1rem;
}
.testimonial__nombre {
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
  font-size: 1.2rem;
}
.testimonial__nombre--small {
  font-weight: 400;
  color: white;
  display: block;
  font-size: 1rem;
  margin-top: 0.5rem;
}

.formulario {
  background: linear-gradient(#181f2a 0 55%, #0b1523 55% 100%);
}
.formulario__contenedor {
  background-color: #1c2431;
  max-width: 90%;
  padding: 3rem;
  text-align: center;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .formulario__contenedor {
    max-width: 50%;
  }
}
.formulario__heading {
  font-size: 2rem;
  font-family: "Raleway", sans-serif;
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .formulario__heading {
    font-size: 2.4rem;
  }
}
.formulario__informacion {
  line-height: 1.7;
  font-size: 1.4rem;
}
@media (min-width: 768px) {
  .formulario__informacion {
    padding-top: 1rem;
    max-width: 85%;
    margin: 0 auto;
  }
}
@media (min-width: 1200px) {
  .formulario__cuerpo {
    display: flex;
    max-width: 85%;
    margin: 3rem auto 0 auto;
    align-items: flex-end;
  }
}
.formulario__input {
  padding: 1.5rem 2.5rem;
  width: 100%;
  border-radius: 3rem;
  border: 0;
  font-size: 1rem;
  margin: 2rem 0;
}
@media (min-width: 1200px) {
  .formulario__input {
    margin-bottom: 0;
    margin-right: 3rem;
  }
}
.formulario__boton {
  background: linear-gradient(to right, #65e2d9, #339ecc);
  font-size: 1.6rem;
  padding: 2rem 3rem;
  border-radius: 4rem;
  font-weight: 700;
  display: block;
  width: 80%;
  text-align: center;
  color: white;
  text-decoration: none;
  border: none;
  margin: 0 auto;
  width: 100%;
  padding: 1.5rem;
}
.formulario__boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .formulario__boton {
    width: auto;
  }
}

.footer {
  background-color: #0b1523;
  padding-top: 10rem;
}
@media (min-width: 768px) {
  .footer {
    padding: 5rem 0 10rem 0;
  }
}
.footer__grid {
  width: 80%;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(9, 1fr);
    align-items: center;
  }
}
.footer__ubicacion {
  grid-column: 1/4;
}
.footer__location-flex {
  display: flex;
  align-items: flex-start;
  margin-top: 3.5rem;
}
.footer__informacion {
  margin: 0 0 0 2rem;
  padding: 0;
  line-height: 1.8;
}
@media (min-width: 768px) {
  .footer__informacion {
    font-size: 1rem;
  }
}
@media (min-width: 1200px) {
  .footer__informacion {
    font-size: 1.4rem;
  }
}
.footer__contacto {
  grid-column: 4/6;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .footer__contacto {
    margin-top: 6.5rem;
  }
}
.footer__telefono, .footer__email {
  display: flex;
  align-items: center;
}
.footer__telefono {
  margin-bottom: 1.5rem;
}
.footer__navegacion {
  display: flex;
  flex-direction: column;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .footer__navegacion {
    align-self: flex-start;
    margin-top: 12.5rem;
  }
}
.footer__link {
  line-height: 2.5;
}
@media (min-width: 768px) {
  .footer__link {
    font-size: 1.2rem;
  }
}
@media (min-width: 1200px) {
  .footer__link {
    font-size: 1.4rem;
  }
}
.footer__social {
  margin-top: 10rem;
  padding-bottom: 5rem;
  display: flex;
  justify-content: center;
  grid-column: 8/10;
}
.footer__redsocial {
  border: 0.5px solid white;
  margin-left: 1.5rem;
  padding: 0.8rem;
  border-radius: 50%;
}
.footer__redsocial::last-of-type {
  margin-left: 0;
}
@media (min-width: 768px) {
  .footer__redsocial {
    margin: 0;
    margin-left: 0.5rem;
  }
}
.footer__icon {
  width: 2rem;
  height: 2rem;
  filter: invert(100%);
}