@use 'variables' as v; 

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
  
body {
    font-family: v.$fuente_principal;
    font-size: 1.4rem;
    background-color: v.$primario1;
}

p {
    font-size: 1.5rem;
    color: v.$blanco;
}
.contenedor {
    width: 95%;
    max-width: 1300px;
    margin: 0 auto;
}

a {
    text-decoration: none;
    color: v.$blanco;
}

img {
    max-width: 100%;
    display: block;
}

h1, h2, h3 {
    margin: 0 0 v.$separacion  0 ;
    color: v.$blanco;
}
h1 {
    font-size: 4rem;
}
h2 {
    font-size: 2.2rem;
    line-height: 1.5;
    font-family: v.$fuente_secundaria; 
}
h3 {
    font-size: 3.2rem;
}
.attribution { 
    font-size: 11px; text-align: center; 
    a {
        color: hsl(228, 45%, 44%); 
    }
}
